.pager-container {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
}

.pager-location {
line-height: 35px;
}

.pager-btn {
  height: 35px;
  width: 35px;
  cursor: pointer;
  background: transparent;
  border-radius: 50%;
  border: none;
  background-color: var(--color2-01);
  font-size: 14px;
  transition: .2s ease;
  outline: none;
}

.pager-btn:hover {
 cursor: pointer;
 color: var(--color4);
 text-shadow: 0px 0px 10px var(--color4);
 transition: .2s
}

