#root {
  height: 100%;
}

.app {
  min-width: 1000px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-size: cover;
  }

.header {
  width: 100%;
  height: 70px;
  }

.main-container {
  display: flex;
  width: 100%;
  height: calc(100% - 70px);
  }
  
.menubar {
  padding: 0 10px;
  border-radius: 15px;
  }
  
.content {
  width: calc(100% - 100px);
  display: flex;
  height: 100%;
  padding: 0px 30px 30px 0;
  }